import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
// import Table from './views/nav1/Table.vue'
// import Form from './views/nav1/Form.vue'
// import user from './views/nav1/user.vue'
import room from './views/nav1/room.vue'
import coupon from './views/nav1/coupon.vue'
import article from './views/nav1/article.vue'
import popUpWin from './views/nav1/popUpWin.vue'
import goods from './views/nav1/goods.vue'
import banner from './views/nav1/banner.vue'

import admin from './views/nav2/admin.vue'
import company from './views/nav2/company.vue'
import member from './views/nav2/member.vue'

import getAllPayOrder from './views/nav3/getAllPayOrder.vue'
import getAllCompleteOrder from './views/nav3/getAllCompleteOrder.vue'
import roomOrder from './views/nav3/roomOrder.vue'



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '信息管理',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/main', component: Main, name: '主页', hidden: true },
            // { path: '/table', component: Table, name: 'Table' },
            // { path: '/form', component: Form, name: 'Form' },
            // { path: '/user', component: user, name: '列表' },
            { path: '/room', component: room, name: '房型管理' },
            { path: '/coupon', component: coupon, name: '优惠券管理' },
            { path: '/article', component: article, name: '文章管理' },
            { path: '/goods', component: goods, name: '兑换商品' },
            { path: '/banner', component: banner, name: '首页轮播图' },
            // { path: '/popUpWin', component: popUpWin, name: '小程序弹窗' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '内部管理',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/company', component: company, name: '公司信息' },
            { path: '/member', component: member, name: '会员管理' },
            { path: '/admin', component: admin, name: '管理员' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '商品积分订单',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/integralGoodOrder', component: integralGoodOrder, name: '商品积分订单' },
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '房间订单管理',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/getAllPayOrder', component: getAllPayOrder, name: '待入住' },
            { path: '/getAllCompleteOrder', component: getAllCompleteOrder, name: '已入住' },
            // { path: '/disposeOrder', component: disposeOrder, name: '处理订单' },
            // { path: '/getAllCompleteOrder', component: getAllCompleteOrder, name: '已完成' },
            // { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


