export const selCoupon = params => { return axios.get(`/api/selCoupon`, { params: params }); }; //获取店家所有的优惠卷
export const putCoupon = params => {
    return axios.post(`/api/back/putCoupon`, qs.stringify(params), {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
        },
    });
}; //发布
